chrome.webNavigation.onCompleted.addListener(
  async () => {
    await chrome.action.openPopup()
    setTimeout(() => {
      chrome.tabs.query({
        active: true,
        currentWindow: true
      }, (tabs) => {
        chrome.scripting.executeScript(
          {
            target: { tabId: tabs[0].id },
            function: async () => {
              const cookies = {}
              document.cookie.split(';').forEach((cookie) => {
                const [key, value] = cookie.split('=')
                // Check for undefined key and empty value
                if (key && value.trim()) {
                  cookies[key.trim()] = value.trim()
                }
              })
              // localStorage kayıt edelim ama chrome üzerinden
              chrome.storage.local.set({ auth_token: cookies.auth_token })
            }
          }
        )
      })
    }, 3500)
  },
  { url: [{ urlMatches: 'https://partner.trendyol.com/' }] }
)
